:root {
  --font-main: "Popins-reg", sans-serif;
  --font-title: "Popins-semb", sans-serif;
  --green-light: #BBFF85;
  --white: #FFFFFF;
  --white-bg: #F3F5F6;
  --black: #0E0C14;
  --black2: #0D0F20;
  --header-input-bg: rgba(154, 94, 245, 0.15);
  --white-opacity: rgba(255, 255, 255, 0.60);
  --purple: #9A5EF5;
  --purple2: #2C0064;
  --purple3: #6202FE;
  --purple-dark: #13012A;
  --purple-grad: linear-gradient(270deg, rgba(72, 51, 107, 0.3) 0%, rgba(40, 29, 59, 0.3) 100%);
  --purple-grad-hover: linear-gradient(270deg, rgba(72, 51, 107, 0.5) 0%, rgba(40, 29, 59, 0.5) 100%);
  --blue-shade: #243A64;
  --blue-gray: #C1C1D4;
  --blue: #1F4AD8;
  --gray: #8E8E98;
  --second-gray: #ECEAF0;
  --red: #C0281C;
}

/* stylelint-disable */
.casino-slot {
  margin: 0 0 20px 0;
}
.casino-slot .casino-slot__item {
  border: 1px solid #858585;
}
.casino-slot__list {
  display: grid;
  grid-template-columns: auto auto auto;
  gap: 15px;
}
.casino-slot__item {
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 12px;
  background: var(--white);
  position: relative;
  max-width: 330px;
}
.casino-slot__item-info {
  position: absolute;
  cursor: pointer;
  top: 20px;
  right: 20px;
}
.casino-slot__item-info-text {
  display: none;
  padding: 15px;
  border-radius: 8px;
  background: linear-gradient(270deg, #0B764F 0%, #0B764F 100%);
  color: var(--white);
  width: 150px;
  position: absolute;
  top: 50px;
  right: 0;
  font-size: 12px;
  line-height: 15px;
}
.casino-slot__item-info-text._show {
  display: block;
}
.casino-slot__item-image {
  max-width: 160px;
  margin: 0 0 10px 0;
}
.casino-slot__item-image img {
  border-radius: 8px;
}
.casino-slot__item-title {
  flex: 1 1 auto;
  font-size: 17px;
  line-height: 26px;
  letter-spacing: -0.004em;
  color: var(--black);
  margin: 0 0 2px 0;
}
.casino-slot__item-licensed {
  display: flex;
  color: #7387BA;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.004em;
  margin: 0 0 20px 0;
}
.casino-slot__item-licensed-one {
  margin: 0 8px 0 0;
}
.casino-slot__item-link {
  align-self: flex-start;
  display: inline-block;
  min-width: 160px;
  text-align: center;
  padding: 12px 30px 10px;
  font-size: 15px;
  line-height: 20px;
}

@media screen and (max-width: 1240px) {
  .casino-slot__list {
    display: grid;
    grid-template-columns: calc(50% - 7.5px) calc(50% - 7.5px);
    gap: 15px;
  }
  .casino-slot__item {
    max-width: 100%;
  }
}
@media screen and (max-width: 1024px) {
  .casino-slot__list {
    grid-template-columns: calc(33.333% - 10px) calc(33.333% - 10px) calc(33.333% - 10px);
  }
}
@media screen and (max-width: 830px) {
  .casino-slot__list {
    grid-template-columns: calc(50% - 7.5px) calc(50% - 7.5px);
  }
}
@media screen and (max-width: 600px) {
  .casino-slot__link {
    width: calc(100% - 30px);
  }
  .casino-slot__list {
    grid-template-columns: 100%;
    gap: 15px;
  }
}